import React, { useState, useEffect } from 'react';
import { formatDistance, parseISO, differenceInSeconds } from 'date-fns';

import styles from './Counter.module.scss';

const formatTwoDigits = (number) => {
  return number.toString().padStart(2, '0');
};

const Counter = ({ targetDate }) => {
  const [currentDate, setCurrentDate] = useState(new Date());

  useEffect(() => {
    const timerId = setInterval(() => {
      setCurrentDate(new Date());
    }, 1000);

    return () => clearInterval(timerId);
  }, []);

  const timeDifference = differenceInSeconds(parseISO(targetDate), currentDate);

  // Calculate the remaining time in days, hours, minutes, and seconds
  const remainingDays = Math.floor(timeDifference / (60 * 60 * 24));
  const remainingHours = Math.floor((timeDifference % (60 * 60 * 24)) / (60 * 60));
  const remainingMinutes = Math.floor((timeDifference % (60 * 60)) / 60);
  const remainingSeconds = timeDifference % 60;

  return (
    <div className={styles.wrapper}>
      <div className='d-flex'>
        <div className={styles.time}>
          <h1>{formatTwoDigits(remainingDays)}</h1>
          <span>Days</span>
        </div>
        <div className={styles.time}>
          <h1>{formatTwoDigits(remainingHours)}</h1>
          <span>Hrs.</span>
        </div>
        <div className={styles.time}>
          <h1>{formatTwoDigits(remainingMinutes)}</h1>
          <span>Mins.</span>
        </div>
        <div className={styles.time}>
          <h1>{formatTwoDigits(remainingSeconds)}</h1>
          <span>Sec.</span>
        </div>
      </div>
    </div>
  );
};

export default Counter;
